<script setup lang="ts">
import useAsync from "~/composables/use-async";
import SubscriptionAPI from "~/services/subscription";
import getErrorLocalePathMessage from "~/utils/locale";

const { t, te } = useI18n();
const route = useRoute();
const router = useRouter();
const toaster = useToast();

const onError = (error: Error) => {
  const { message } = getErrorLocalePathMessage(error.message, { t, te });
  return toaster.error(message);
};
const onPaySuccess = () => {
  toaster.success(t("pages.events.success_purchased_secondary"));
  router.push("/account/subscription/");
};

const {
  data: subscription,
  loading: loadingSubscription,
  run: loadSubscription,
} = useAsync(() => SubscriptionAPI.loadById(route.params.id as string), { onError });

onMounted(() => loadSubscription());
</script>

<template>
  <div class="text-main text-body-medium font-bold min-h-6 mb-6">
    {{ loadingSubscription ? t('labels.loading') : subscription?.title }}
  </div>
  <BillingForm
    v-if="subscription"
    :subscription="subscription"
    @success="onPaySuccess"
    @error="onError"
  />
</template>
